:root {
  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #0ea0ff;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #6091ef;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #343a40;
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
  --font-family-sans-serif: "Lato",sans-serif;
  --font-family-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace; }

.frame {
  position: absolute;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0; }

html {
  height: 100%;
  display: block; }

input {
  width: 100%;
  border-top-style: hidden;
  border-left-style: hidden;
  border-right-style: hidden;
  padding-top: 1rem; }

input:focus {
  outline: 0px; }

body {
  margin: 0;
  font-family: Lato,sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  display: flex;
  color: #212529;
  background-color: #fff;
  overflow: hidden;
  height: 100%;
  padding: 0; }

.wrapper {
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow: hidden; }

.blkWrapper {
  display: flex;
  flex: 1;
  overflow: hidden;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem; }
